'use client';

import React, { useState } from 'react'
import { IoIosArrowBack } from 'react-icons/io'

interface NewAppFormProps {
    onCancel: () => void;
    onCreate: (app: { name: string; description: string }) => void;
}

const NewAppForm: React.FC<NewAppFormProps> = ({ onCancel, onCreate }) => {
    const [appName, setAppName] = useState('');
    const [appDescription, setAppDescription] = useState('');

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        if (!appName.trim() || !appDescription.trim()) {
            alert("Please fill out all fields.");
            return;
        }
        onCreate({ name: appName, description: appDescription });
    };

    return (
        <div className="max-w-2xl mx-auto">
            <form onSubmit={handleSubmit} className="space-y-6 bg-white dark:bg-gray-800 p-8 rounded-2xl shadow-lg">
                <div>
                    <label htmlaFor="appName" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">App Name</label>
                    <input
                        type="text"
                        id="appName"
                        value={appName}
                        onChange={(e) => setAppName(e.target.value)}
                        className="w-full px-4 py-3 bg-gray-100 dark:bg-gray-700 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition"
                        placeholder="My Awesome App"
                    />
                </div>
                <div>
                    <label htmlFor="appDescription" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">Description</label>
                    <textarea
                        id="appDescription"
                        value={appDescription}
                        onChange={(e) => setAppDescription(e.target.value)}
                        rows={4}
                        className="w-full px-4 py-3 bg-gray-100 dark:bg-gray-700 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition"
                        placeholder="Describe what your app does..."
                    ></textarea>
                </div>
                <div className="flex justify-end pt-4">
                    <button type="submit" className="px-8 py-3 bg-blue-600 text-white font-semibold rounded-lg shadow-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 dark:focus:ring-offset-gray-900 transform hover:scale-105 transition-all duration-300">
                        Post App
                    </button>
                </div>
            </form>
        </div>
    );
};



export default NewAppForm